@font-face {
  font-family: 'JetBrains Mono';
  font-style: italic;
  font-weight: 800;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/jetbrainsmono/v1/tDba2o-flEEny0FZhsfKu5WU4xD-IQ-PuZJJXxfpAO8LeVOQ.ttf) format('truetype');
}
@font-face {
  font-family: 'JetBrains Mono';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url(https://fonts.gstatic.com/s/jetbrainsmono/v1/tDbY2o-flEEny0FZhsfKu5WU4zr3E_BX0PnT8RD8yKxjPQ.ttf) format('truetype');
}
/*Colors*/
body {
  background: url('../pictures/home_bg.jpg') center;
  background-size: auto;
  font-family: 'JetBrains Mono', monospace;
  font-weight: 400;
  font-size: 20px;
}
body h2,
body h4 {
  font-family: 'JetBrains Mono', monospace;
  font-weight: 900;
}
header {
  position: fixed;
  z-index: 50;
  width: 100%;
  top: 0;
  background: #808080;
}
header .navbar-brand {
  margin-bottom: 0;
}
header a {
  color: #ffffff;
}
header a:hover {
  cursor: pointer;
}
main {
  margin-top: 20px;
}
main .content {
  margin-top: 100vh;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;
  background: #ffffff;
}
main section {
  border-bottom: 2px solid #808080;
  min-height: 100vh;
  padding: 20px;
  padding-top: 100px;
}
main h2 {
  margin-bottom: 30px;
}
main .home {
  background: url('../pictures/home_bg.jpg') bottom no-repeat;
  background-size: cover;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: -10;
}
main .home-logo {
  margin-left: 20px;
}
main .home .greetings {
  background-color: #ffffff;
  padding: 10px;
  text-align: center;
  border-radius: 50px;
  min-width: 370px;
  height: 250px;
  position: relative;
  display: inline-block;
}
main .home .greetings #nav-buttons-home {
  margin-top: 20px;
  position: absolute;
  bottom: 20px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
main .home .greetings h2,
main .home .greetings h4,
main .home .greetings p {
  margin-left: 0;
}
main .home .greetings:after {
  content: ' ';
  position: absolute;
  width: 0;
  height: 0;
  left: 100%;
  top: 30%;
  border: 35px solid;
  border-color: #ffffff transparent transparent #ffffff;
}
main .home .next-page-btn {
  position: absolute;
  bottom: 30px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
main .home .next-page-btn svg {
  color: #ffffff;
  -webkit-transition: all 1s;
  transition: all 1s;
}
main .home .next-page-btn svg:hover {
  color: #c0c0c0;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  z-index: 5;
}
main .home .next-page-btn svg:focus {
  border: none;
}
main .home .next-page-btn svg:target {
  border: none;
}
main .about .card {
  border: none;
  margin-bottom: 20px;
}
main .about .about-block {
  position: relative;
  min-height: 70vh;
  margin: 10px;
  padding: 20px;
  border: 1px solid #808080;
  border-radius: 10px;
  z-index: 0;
  background: #808080;
  -webkit-transition: all 1s;
  transition: all 1s;
}
main .about .about-block:hover {
  background: #c0c0c0;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
  z-index: 5;
  overflow-x: visible;
}
main .about .about-block .btn {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}
main .portfolio img {
  width: 100%;
}
main .portfolio .accordion-button:not(.collapsed) {
  background-color: #808080;
  color: #ffffff;
}
footer {
  background: #808080;
  min-width: 360px;
}
footer p {
  margin-bottom: 0;
}
.content {
  min-width: 360px;
}
@media (max-width: 767px) {
  main .home .greetings {
    border-radius: 0px;
    width: 100%;
    min-width: 100;
  }
  main .home .greetings:after {
    border: none;
  }
  main .home .greetings-logo {
    display: none;
  }
  .home-logo {
    display: none;
  }
}
